.error-page_wrap {
  height: calc(100vh - 116.1px);
  background-color: #fff;
}

.error-page_wrap .error-page_inner {
  height: 100%;
}

.error-page_wrap .error-page_inner .error-page_content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.error-page_wrap .error-page_inner .error-page_content .top {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 40px;
  text-align: center;
}

.error-page_wrap .error-page_inner .error-page_content h1 {
  font-size: 190px;
  text-transform: uppercase;
  text-align: center;
  color: #23a8cd;
  line-height: 0.7;
  margin-bottom: 50px;
}

.error-page_wrap .error-page_inner .error-page_content .to-main {
  display: block;
  width: 135px;
  font-size: 15px;
  font-weight: 500;
  color: #fff;
  text-align: center;
  padding: 15px 0;
  background-color: #23a8cd;
  border-radius: 12px;
  box-shadow: 0 8px 24px 0 rgba(24, 120, 147, 0.24);
}

.error-page_wrap .error-page_inner .error-page_content .to-main:hover {
  color: #fff !important;
}

@media screen and (max-width: 990px) {
  .error-page_wrap {
    height: calc(100vh - 75.13px);
  }
}

@media screen and (max-width: 500px) {
  .error-page_wrap {
    height: calc(100vh - 83.66px);
  }
  .error-page_wrap .error-page_inner .error-page_content .top {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  .error-page_wrap .error-page_inner .error-page_content h1 {
    font-size: 150px;
    margin-bottom: 30px;
  }
}

